.back {

  &__btn {
    display: flex;
    align-items: center;
    max-width: fit-content;
    gap: 10px;
    color: var(--color-light);
  }

  &__icon {
    display: inline-flex;
    padding: 4px;
    width: 32px;
    height: 32px;
    transform: rotate(90deg);
    border-radius: 100%;
    background-color: var(--color-ebony-clay);
    color: var(--color-sundance);
    transition: background-color 0.3s ease-in-out;

    svg {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }


  }

  &__btn:hover &__icon {
    background-color: #41424D;
  }

}
